.more{
        height: auto;
        width: auto;
        padding: 20px;
        text-align: center;
        border-radius: 12px;
        background-color: rgba(240, 248, 255, 0.241);
        margin: 20px 0;

        ul{
            padding: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            justify-content: center;
            li{
                img{
                    width: 40px;
                    padding: 10px;
                }
            }
        }
    
}